.contenedor-modal-mostrar{
    background-color: rgba(0,0,0,.8);
    position:fixed;
    top:0;
    right:0;
    bottom:0;
    left:0; 
    z-index: 1000;
    overflow-x: hidden;
    overflow-y: auto; 
}

.contenedor-modal-mostrar .contenido-modal{
    width:100%;
    height: 340px;
}

.contenedor-modal-mostrar .contenido-modal .slide{
    margin-top:20px;
    margin-left:50px;
}

.contenedor-modal-mostrar .contenido-modal .icono-cerrar{
    float: right;
    margin-right:25px;
    width:25px;
    height:25px;
    background: red;
}


.contenedor-modal-mostrar .contenido-modal .slide-efecto img{
    width: 97%;
    height:450px;
}

.contenedor-modal-ocultar{
    display: none;
}

.contenedor-modal-mostrar .contenido-modal h1{
    margin-left:40px;
    margin-top:25px;
    color: white;
    padding: 10px;
    font-size:22px; 
}

.contenedor-modal-mostrar .contenido-modal .descripcionPublicacion{
    margin-left:265px;
    margin-top: 25px;
    width: 50%;
    height: 110px;
    border: 2px solid black;
    border-radius: 15px;
    font-size:18px;
    padding:8px;
}

/** Estilo para el formulario*/

.contenedor-modal-mostrar .contenido-modal .contenedor-formulario{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-left:300px;
    margin-top:50px;   
}

.contenedor-modal-mostrar .contenido-modal .contenedor-formulario h2{
    background: rgba(0, 0, 0, 0.2);
    color: white;
    padding: 15px;
    width: 100%;
    text-align: center;
}

.contenedor-modal-mostrar .contenido-modal .contenedor-formulario form{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;   
    background: rgba(0, 0, 0, 0.5);
    width:600px;
}

.contenedor-modal-mostrar 
.contenido-modal
.contenedor-formulario
form input{
    text-align: center;
    margin: 10px;
    padding:10px;
    width:90%;
    border: 2px solid black;
    border-radius:15px;
}

.contenedor-modal-mostrar 
.contenido-modal
.contenedor-formulario
form textarea{
    margin: 10px;
    width: 90%;
    height:100px;
    border: 2px solid black;
    border-radius:15px;
    padding: 10px;
    font-size:17px;
}

.contenedor-modal-mostrar 
.contenido-modal
.contenedor-formulario
form .btn-enviar{
    border-radius:5px;
    background:#00C412;
    padding:15px;
    color: white;
    font-size: 18px;
    margin: 10px;
    cursor: pointer;
}

.contenedor-modal-mostrar 
.contenido-modal
.contenedor-formulario
form .btn-enviar:hover{
    opacity: 0.90;
}

.contenedor-modal-mostrar .contenido-modal span{
    color:#d3ad7f;
    margin-left: 5px;

}

*{
