/*estilo para la pantalla modal*/

.contenedor-modal-publicaciones{
    background-color: rgba(0,0,0,.8);
    position:fixed;
    top:0;
    right:0;
    bottom:0;
    left:0; 
    z-index: 1000;
    overflow-x: hidden;
    overflow-y: auto;  
}

.contenedor-no-modal{
    display: none;

}

.contenedor-modal-publicaciones .contenido-modal{
    width:700px;
    margin-top: 100px;
    margin-left:300px;
    padding: 20px;
}

/*estilo para la imagen de cerrar-modal*/

.contenedor-modal-publicaciones .contenido-modal img{
    background: red;
    float: right;
    cursor: pointer;
}

/*estilo para el formulario*/

.contenedor-modal-publicaciones .contenido-modal form{
    background: rgba(0, 0, 0, 0.5);  
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width:600px;
    padding: 20px;
    border: 3px solid black;
}

.contenedor-modal-publicaciones .contenido-modal form h2{
    color: white;
    padding-bottom: 15px;  
}

/*estilo para los inputs y text-area*/

.contenedor-modal-publicaciones .contenido-modal 
form input,textarea{
    text-align:start;
    margin: 10px;
    padding:10px;
    width:90%;
    border: 2px solid black;
    border-radius:15px;
    font-size: 18px;
}

.contenedor-modal-publicaciones .contenido-modal 
form input::-webkit-input-placeholder, textarea::-webkit-input-placeholder{
    text-align: center;
}

.contenedor-modal-publicaciones .contenido-modal 
form input::placeholder,textarea::placeholder{
    text-align: center;
}


.contenedor-modal-publicaciones .contenido-modal 
form textarea{
    height:100px;
}


/*estilos para el input type-file*/
.file-select {
    position: relative;
    margin: 10px;
    width:90%;
    height:45px;
    
  }

  .file-select::before {
    background-color: #5678EF;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid black;
    border-radius: 15px;
    content: 'Subir imagen'; /* testo por defecto */
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
  }

  .file-select input[type="file"] {
    opacity: 0;
    display: inline-block;
    cursor: pointer;
  }

  /*estilos para el select*/

.contenedor-modal-publicaciones .contenido-modal 
form .combo-box{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 15px;
    width:550px;
}

.contenedor-modal-publicaciones .contenido-modal 
form .combo-box select{
    padding:8px;
    margin: 6px;
    width:100%;
    border: 2px solid black;
    border-radius: 10px;
    color:black;
    font-size: 12px;
}

/*estilos para los botones*/

.contenedor-modal-publicaciones .contenido-modal 
form .btn-actualizar{
    text-align: center;
    border-radius:7px;
    background:#00C412;
    padding:15px;
    color: white;
    font-size: 18px;
    cursor: pointer;
    margin: 20px;
}


.contenedor-modal-publicaciones .contenido-modal 
form .btn-actualizar:hover{
    opacity: 0.90;
}

.contenedor-modal-publicaciones .contenido-modal 
form .btn-eliminar{
    border-radius:7px;
    background:#F62620;
    padding:15px;
    color: white;
    font-size: 18px;
    cursor: pointer;
    text-align: center;
}


.contenedor-modal-publicaciones .contenido-modal 
form .btn-eliminar:hover{
    opacity: 0.90;
}


