* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.contenedor-menu_horizontal {
  position: -webkit-sticky;
  position: sticky;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 150px;
  background: #121212;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
}



/**estilos para el icono del menu*/

.contenedor-menu_horizontal .icono-menu {
  display: flex;
  justify-content:center;
  align-items: center;
  cursor: pointer;
  margin:15px;
}

.contenedor-menu_horizontal .icono-menu img {
  width: 65px;
  height:65px;
}

.contenedor-menu_horizontal .contenedor-items {
  display: flex;
  flex-direction:row;
  justify-content: center;
  align-items: center;
}

/**estilos para el logo*/
.contenedor-menu_horizontal .contenedor-items .contenedor-logo {
  display: flex;
  align-items: center;
  margin:50px;
}

.contenedor-menu_horizontal .contenedor-logo h2 {
  color: white;
  margin: 12px;
  
}

.contenedor-menu_horizontal .contenedor-logo .logo {
  width:85px;
  height:85px;
}

/*estilos para el input*/
.contenedor-menu_horizontal .contenedor-input{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left:100px;
}

.contenedor-menu_horizontal .contenedor-input input{
  padding: 8px;
  text-align: center;
}

.contenedor-menu_horizontal .contenedor-input button{
  width: 100px;
  background: #00C412;
  color: white;
  cursor: pointer;
  padding: 10px;
}

/**estilo para el icono iniciar session*/

.contenedor-menu_horizontal .contenedor-items .contenedor-icono-inicarSession {
  display: flex;
  justify-content: center;
  align-items: center;
 
}

.contenedor-menu_horizontal
  .contenedor-items
  .contenedor-icono-inicarSession
  img {
  cursor: pointer;
}

.contenedor-menu_horizontal .contenedor-items .contenedor-redes-sociales {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left:200px;
}

.contenedor-menu_horizontal .contenedor-items .contenedor-redes-sociales img {
  width:55px;
  height:55px;
  margin-left: 20px;
  cursor: pointer;
}

@media (max-width: 480px) {
  .contenedor-menu_horizontal {
    width:100%;
    height: 100%;
  }

  .contenedor-menu_horizontal .contenedor-logo {
    display: none;
  }

  .contenedor-menu_horizontal .contenedor-items .contenedor-input {
    display: none;
  }

  .contenedor-menu_horizontal .contenedor-items .contenedor-redes-sociales {
    position: relative;
    right:250px;
  }

  .contenedor-menu_horizontal .contenedor-items .contenedor-redes-sociales img {
    width: 40px;
    height: 40px;
  }
}

